@import url('https://fonts.googleapis.com/css?family=Lato:100,100i,300,300i,400,400i,700,700i,900,900i');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}
html {
  font-size: 10px;
}
html, body {
  height: 100%;
}
@media screen and (min-width:1200px) {
  .navbar-top {
    height: 4.4rem;
    background-color: rgb(248, 248, 248);
    box-shadow: 0 3px 15px rgba(0, 0, 0, 0.25);
    position: relative;
  }
  .navbar-item {
    display: inline-block;
    padding: 1rem;
    .navbar-link {
      text-decoration: none;
      font-size: 2rem;
      color: rgb(216, 31, 68);
      font-weight: 500;
      transition: all 0.3s ease;
      &:hover {
        color: rgb(236, 42, 81);
        transition: all 0.3s ease;
      }
    }
  }
  .navbar-item:hover .navbar-dropdown {
    display: block;
    position: absolute;
    height: 400px;
    top: 4.4rem;
    left: 0;
    right: 0;
    font-size: 1.6rem;
    background-color: rgb(216, 31, 68);
    color: white;
    box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.2);
    h2 {
      font-weight: 700;
      margin: 1rem 0;
    }
    .navbar-left {
      display: inline-block;
      padding: 2rem;
      padding-right: 0;
      border-right: 1.5px solid white;
      width: 250px;
      height: 350px;
    }
    .navbar-dropdown-li:hover .navbar-right {
      padding: 2rem;
      position: absolute;
      display: inline-block;
      left: 250px;
      top: 0;
      right: 0;
      .navbar-right-ul {
        list-style: none;
        .navbar-right-li {
          padding: 0.2rem 0.4rem;
          &:hover {
            .navbar-right-link {
              text-decoration: underline;
            }
          }
        }
        .navbar-right-link {
          text-decoration: none;
          color: white;
        }
      }
    }
    .navbar-right {
      display: none;
    }
    .navbar-dropdown-ul {
      list-style: none;
    }
    .navbar-dropdown-li {
      margin: 0.2rem 0;
      .li-text {
        display: inline-block;
        padding: 0.5rem 1rem;
      }
    }
    .navbar-dropdown-li:hover {
      background-color:white;
      box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.25);
      color: rgb(216, 31, 68);
    }
    .navbar-right-h2 {
      color: white;
    }
    .navbar-dropdown-link {
      color: white;
      font-weight: 300;
      text-decoration: none;
    }
  }
  .navbar-dropdown {
    display: none;
  }
}
@media screen and (max-width: 1200px) {
  .navbar {
    display: none;
  }
}